import React from "react";

export default function (Component) {
    return class Mountable extends React.PureComponent<{
        onMounted: (ID: any) => void;
        ID: any;
        [prop: string]: any;
    }> {
        componentWillMount() {
            if (this.props.onMounted) this.props.onMounted(this.props.ID);
        }
        render() {
            const { ID, onMounted, ...remain } = this.props;
            return <Component {...remain} />
        }
    }
}