export default function (prevItems: Product[] = [], action: Action) {
    const item: Product = action.payload;
    switch (action.type) {
        case "ITEM_CHANGED":
            let PrevItem = prevItems.find(x => x.id === item.id);
            if (!PrevItem && item.cantidad)
                return [...prevItems, item];

            if (PrevItem) {
                const items = [...prevItems];
                let PrevIndex = items.indexOf(PrevItem);
                if (item.cantidad) {
                    items[PrevIndex] = item;
                } else {
                    items.splice(PrevIndex, 1);
                }
                return items;
            }
            return [...prevItems];

        default:
            return [...prevItems];
    }
}