import React from "react";
import { Dialog, DialogContent, Typography, TextField, DialogActions, Button } from "@material-ui/core";
import { HorariosToDias } from "../../Services/HorarioCalcs";

export default class Schedule extends React.PureComponent {
    state = {
        dia: "",
        hora: "",
        dias: []
    }
    constructor(props) {
        super(props);
        this.handleDia = this.handleDia.bind(this);
        this.handleHora = this.handleHora.bind(this);
        this.handleClose = this.handleClose.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        if (props.value) {
            for (const key in this.state) {
                if (props.value.hasOwnProperty(key)) {
                    this.state[key] = props.value[key];
                }
            }
        }
        this.state.dias = HorariosToDias(props.store && props.store.horarios, 7);
    }
    handleDia(event) {
        const { value } = event.currentTarget;
        this.setState({ dia: value });
        var horas_del_dia = HorasDelDia(value, this.state.dias);
        if (!(horas_del_dia &&
            horas_del_dia.length &&
            horas_del_dia.indexOf(+this.state.hora) >= 0)
        ) this.setState({ hora: null });
    }
    handleHora(event) {
        const { value } = event.currentTarget;
        this.setState({ hora: value });
    }
    handleSubmit(event) {
        event.preventDefault();
        // si no le quiere poner hora, esta bien!
        //var horas_del_dia = HorasDelDia(this.state.dia, this.state.dias);x
        this.props.onDone && this.props.onDone({
            dia: this.state.dia,
            hora: this.state.hora
        });
    }
    handleClose() {
        this.props.onDone && this.props.onDone(this.props.value);
    }

    render() {
        const { dia, hora } = this.state;
        var dias = (this.state.dias && this.state.dias.length && this.state.dias) || [];
        var horas = [{ value: "", label: "Cualquier hora" }, ...HorasDelDia(dia, dias)];
        var dia_opciones = [{ day: "", label: "Elige un da" }, ...dias];

        return (<Dialog open={true} onClose={this.handleClose}>
            <form onSubmit={this.handleSubmit}>
                <DialogContent>
                    <Typography variant="h6">Elige el horario para tu entrega:</Typography>

                    <TextField
                        select
                        value={dia || ""}
                        onChange={this.handleDia}
                        variant="outlined"
                        style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
                        SelectProps={{ native: true }}
                    >
                        {dia_opciones.map(d => <option value={d.day} >{d.label || d.Date.toDateString()}</option>)}
                    </TextField>

                    {horas && horas.length && <TextField
                        select
                        value={hora || ""}
                        onChange={this.handleHora}
                        variant="outlined"
                        style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
                        SelectProps={{ native: true }}
                    >
                        {horas.map(h => <option value={h.value} >{h.label}</option>)}
                    </TextField>}
                </DialogContent>
                <DialogActions>
                    <Button type="submit">GUARDAR</Button>
                </DialogActions>
            </form>
        </Dialog>)
    }
}


function HorasDelDia(dia, dias) {
    var selectedDia = dia && dias.find(item => item.day === dia);
    if (selectedDia) {
        return (selectedDia.horas && selectedDia.horas.map(x => {
            var HORAS_DEC = x / 60;
            var HORA = parseInt(HORAS_DEC);
            var MIN = HORAS_DEC > HORA ? (HORAS_DEC - HORA) * 60 : 0;
            var Day_ADS = 0;
            if (HORA > 23) {
                Day_ADS++;
                HORA = HORA - 24;
            }
            return {
                value: x,
                label: `${HORA.toString().length === 1 ? "0" : ""}${HORA}` + ":" + `${MIN.toString().length === 1 ? "0" : ""}${MIN} ${Day_ADS ? ("+" + Day_ADS) : ""}`
            }
        })) || [];
    }
    return [];
}