import { createStore, combineReducers } from "redux";
import productList from "./Reducers/productList";
import items from "./Reducers/items";

export class State {
    // este lo tengo que quitar
    activeStep = 0;

    items: Product[] = [];
    productList: Product[] = [];
    forma_pago = "";
    numeroTransaccion = "";

    nota = "";
    // con esta funcion se si la direccion fue aprobada o no!
    delivery = null;
    contactData = {
        telefono: "",
        nombre: "",
        email: "",
        documento: 2,
        identificacion: ""
    };
    invoiceData = null;
    direccion = {
        direccion: "",
        referencia: "",
        position: null
    };
    envio_programado = null;
    start_timestamp = new Date().valueOf();
    orderNumber = null;
}


export function Create(defaultState: State = new State()) {
    return createStore(combineReducers({
        productList,
        items
    }));
}
