import React from "react";
import {
    Dialog, DialogContent, Typography, IconButton, TextField,
    Button, DialogActions, CardMedia,
    withStyles, withMobileDialog,
    AppBar, Toolbar, Hidden, Slide,
} from "@material-ui/core";
import { Add, Remove } from "@material-ui/icons";
import { Route } from "react-router-dom";
const styles = theme => ({
    dialogPaper: {
        maxWidth: 450,
        width: "100%",
        maxHeight: "calc(100% - 20px)",
        marginTop: 10,
        marginBottom: 10
    },
    loadingPaper: {
        marginLeft: 10,
        marginRight: 10,
        width: "calc(100% - 20px)",
        maxWidth: 450
    }
});

interface Props {
    show: boolean;
    onDone: (item?: Product) => void;
    product?: Product | null | undefined;
    classes?: any;
    fullScreen?: boolean;
}
interface State extends Product {
    loading: boolean;
}

class ViewProduct extends React.Component<Props, State> {
    state = {
        nombre: "",
        descripcion: "",
        pvp_final: 0,
        cantidad: 1,
        imgSrc: "",
        nota: "",
        loading: true,
        idGrupo: 0,
        grupo: ""
    }

    shouldComponentUpdate(nextProps: Props) {
        return (this.props.show === nextProps.show && this.props.show === false) ? false : true;
    }
    componentWillReceiveProps(nextProps: Props) {
        if (!this.props.show && nextProps.show) {
            this.setState(prev => {
                const keys = Object.keys(prev);
                // RESETEO LAS PROPIEDADES QUE NO EXISTEN
                var cantidad = (nextProps.product || prev).cantidad;
                if (cantidad === null || cantidad === undefined) cantidad = 1;

                return {
                    ...keys.reduce((acc, v) => ({ ...acc, [v]: undefined }), {} as State)
                    , ...nextProps.product
                    , cantidad
                    , loading: false
                }
            });
        }
    }

    handleCantidad = event => {
        const name = event.currentTarget.name;
        this.setState(prev => {
            const newState = { ...prev };
            let cantidad = newState.cantidad;
            if (name === "add") cantidad = (cantidad || 0) + 1;
            if (name === "remove") cantidad = cantidad <= 0 ? 0 : (cantidad - 1);
            newState.cantidad = cantidad;
            return newState;
        });
    }
    handleInput = event => {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value } as any);
    }
    handleDone = () => this.props.onDone({ ...this.state });
    handleExit = () => this.props.onDone();

    render() {
        const { state } = this;
        const { classes, show, fullScreen } = this.props;
        const DialogProps: any = {};
        if (!fullScreen) DialogProps.classes = { paper: classes.dialogPaper };

        console.info("Rendering ViewProducts");

        const pvp = ((state.pvp_final && state.cantidad)
            ? (state.pvp_final * state.cantidad)
            : state.pvp_final).toFixed(2);

        return (<Dialog
            fullScreen={fullScreen}

            {...DialogProps}
            sm
            open={show}
            scroll="paper"
            TransitionComponent={Transition}
            onClose={this.handleExit}
        >
            <Hidden smUp>
                <AppBar position="static">
                    <Toolbar>
                        <Typography color="inherit" style={{ flexGrow: 1, textTransform: "uppercase" }} variant="h6">{state.nombre}</Typography>
                    </Toolbar>
                </AppBar>
            </Hidden>

            <DialogContent style={{ padding: 0 }}>
                <CardMedia className="product-details-img" image={state.imgSrc || "https://diegopq95.github.io/img/default.png"} title={state.nombre} />

                <Hidden xsDown>
                    <div style={{ width: "100%", display: "flex", flexDirection: "row" }}>
                        <Typography style={{ margin: 10, flex: 1, textTransform: "uppercase" }} variant="h6">{state.nombre}</Typography>
                        <Typography style={{ margin: 10 }} variant="h6">$ {pvp}</Typography>
                    </div>
                </Hidden>

                <Typography style={{ margin: 10 }} variant="body2">
                    {state.descripcion}
                </Typography>

                <div style={{ width: "100%", textAlign: "center", marginTop: 20, marginBottom: 20, display: "table" }}>
                    <Hidden smUp>
                        <Typography style={{ verticalAlign: "middle", display: "table-cell" }} variant="h6">$ {pvp}</Typography>
                    </Hidden>

                    <div style={{ display: "table-cell", verticalAlign: "middle" }} >
                        <QuantityButton onClick={this.handleCantidad} name="add">
                            <Add />
                        </QuantityButton>
                        <Typography style={{ display: "inline-block", marginLeft: 20, marginRight: 20 }} variant="h6">
                            {state.cantidad || 0}
                        </Typography>
                        <QuantityButton disabled={state.cantidad <= 0} onClick={this.handleCantidad} name="remove">
                            <Remove />
                        </QuantityButton>
                    </div>
                </div>

                <div style={{ width: "95%", margin: "auto" }}>
                    <TextField value={state.nota}
                        inputProps={{ maxLength: 200 }}
                        onChange={this.handleInput}
                        name="nota"
                        placeholder="Ej. Sin Salsa, verduras aparte, etc."
                        label="Agrega una nota:"
                        variant="outlined"
                        multiline rows="3" fullWidth />
                </div>
            </DialogContent>
            <DialogActions>
                <Button type="button" onClick={this.handleExit} variant="text">SALIR</Button>
                <Button type="button" onClick={this.handleDone} color="primary" variant="outlined">AGREGAR</Button>
            </DialogActions>
        </Dialog>);
    }
}
const ViewProductHOC = withStyles(styles)(withMobileDialog()(ViewProduct));
export default function (props: {
    path: string;
    onDone: (item?: Product) => void;
    findProductFn: (ID?: any) => Product | null | undefined;
}) {
    return <Route
        path={props.path}
        children={({ match }) => <ViewProductHOC
            show={Boolean(match && match.params.id)}
            onDone={props.onDone}
            product={props.findProductFn(match && match.params && match.params.id)}
        />}
    />
}

function QuantityButton(props) {
    const style = { height: 50, width: 50, border: "1px solid gray" };
    if (props.disabled) delete style.border;
    return (<IconButton
        disabled={props.disabled}
        onClick={props.onClick}
        name={props.name} style={style}
    >
        {props.children}
    </IconButton>)
}

const Transition = React.forwardRef(function (props: any, ref: any) {
    return <Slide direction="up" {...props} ref={ref} />;
});