import React from "react";
import { TextField, Typography, Divider } from "@material-ui/core";
import Mountable from "../HOC/Mountable";
import { connect } from "react-redux";
import Totales from "../Services/Totales";

function Payment(props) {
    const Items = [...props.items];
    if (props.delivery && props.delivery.status === "ACTIVO" && !isNaN(+props.delivery.valor)) {
        Items.push({
            pvp: props.delivery.valor,
            cantidad: 1,
            id: "DOMICILIO",
            pagaIva: false
        });
    }
    const resume = (Totales(Items, props.store.porcentajeIVA) || { total: 0 });
    return (<div style={{ flex: 1, textAlign: "center", maxWidth: "95%", margin: "auto" }}>
        <Typography style={{ margin: 10 }} variant="h4">TOTAL: ${resume.total.toFixed(2)}</Typography>
        <Divider style={{ marginBottom: 20, marginLeft: 30, marginRight: 30 }} />
        <TextField
            select
            value={props.forma_pago || ""}
            name="forma_pago"
            onChange={props.onInputChange}
            variant="outlined"
            style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
            SelectProps={{ native: true }}
        >
            <option value="">- SELECCIONA TU MTODO DE PAGO -</option>
            {MapFormasPago(props.store.formasPago).map(x => (<option key={x.value} value={x.value} >{x.label}</option>))}
        </TextField>
        {+props.forma_pago === 3 ? (<div>
            {props.store.datosTransferencia && <Typography style={{
                textAlign: "left", margin: "10px 20px", whiteSpace: "pre-line"
            }} variant="subtitle1">{props.store.datosTransferencia}</Typography>}
            <Typography variant="body2">Realiza tu transferencia y luego ingresa el Numero de Comprobante aqui:</Typography>
            <TextField
                label="Comprobante de Transaccin"
                variant="outlined"
                value={props.numeroTransaccion || ""}
                placeholder="Agrega tu # de Comprobante"
                name="numeroTransaccion"
                inputProps={{
                    "aria-autocomplete": "none",
                    "autocomplete": "off"
                }}
                onChange={props.onInputChange}
                type="phone"
                autoComplete="off"
                style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
            />
        </div>) : null}
    </div>);
}
function MapFormasPago(info) {
    var all = [] as any[];
    if (!info) return all;
    if (info.indexOf(1) >= 0) all.push({ label: "Efectivo", value: 1 });
    if (info.indexOf(2) >= 0) all.push({ label: "Tarjeta de Credito/Debito", value: 2 });
    if (info.indexOf(3) >= 0) all.push({ label: "Transferencia Bancaria", value: 3 });
    return all;
}


const mapStateToProps = state => {
    return {
        items: state.items
    }
}
export default Mountable(
    connect(mapStateToProps)(Payment)
);