import React from "react";
import { Typography } from "@material-ui/core";
import ViewProduct from "./ViewProduct";
import Carousel from "./Carousel";
import { ItemChanged } from "../../ActionCreators/Items";
import { connect } from "react-redux";
import Mountable from "../../HOC/Mountable";


interface Props {
    onItemChange: (product: Product) => void;
    path: string;
    choosenItems: Product[];
    style: any;
    productList: Product[];
    history?: any;
}

class TakeOrder extends React.PureComponent<Props, any> {
    handleClick = (event: React.MouseEvent<any, MouseEvent>) => {
        this.props.history.push(this.props.path + "/view-product/" + event.currentTarget.id);
    }
    findProduct = (ID?) => {
        if (!ID) return undefined;
        var exists = (this.props.choosenItems || []).find(x => x.id === +ID);
        if (exists) return exists;
        return this.props.productList.find(x => x.id === +ID);
    }
    productViewClose = product => {
        if (product) this.props.onItemChange(product);
        this.props.history.push(this.props.path);
    }
    render() {
        const { productList, style, path, choosenItems } = this.props;

        const products = productList.reduce((prev, current) => {
            let exists = prev.find(x => x.group === current.grupo);
            if (!exists) {
                exists = {
                    group: current.grupo,
                    id: current.idGrupo,
                    orden: current.grupo_orden,
                    items: []
                }
                prev.push(exists);
            }
            exists.items.push(current);
            return prev;
        }, [] as any[]);

        if (!(products && products.length)) {
            return (<div style={{ ...style, textAlign: "center" }}>
                <Typography variant="h6">Esta tienda no tiene productos disponibles. Intenta nuevamente despus.</Typography>
            </div>)
        }
        return (<div style={{ ...style, textAlign: "left", overflowX: "hidden", overflowY: "auto" }}>
            <ViewGroup products={products} choosenItems={choosenItems} onClick={this.handleClick} />
            <ViewProduct
                path={path + "/view-product/:id"}
                onDone={this.productViewClose}
                findProductFn={this.findProduct}
            />
        </div >);
    }
}

function ViewGroup(props) {
    const ID = props.id;
    const products = props.products.filter(x => x.idGrupo === +ID
        || (ID === undefined || ID === null)
    );
    return products
        .sort((a, b) => a.orden - b.orden)
        .map((p, index) => <Carousel key={p.id || index}
            onClick={props.onClick}
            style={{ marginTop: 10, marginBottom: 10 }}
            title={p.group}
            items={p.items}
            choosenItems={props.choosenItems}
        />);
}


const mapStateToProps = state => {
    return {
        productList: state.productList,
        choosenItems: state.items
    }
}
const mapDispatchToProps = {
    onItemChange: ItemChanged
}

export default Mountable(
    connect(mapStateToProps, mapDispatchToProps)(TakeOrder)
);