import React from "react";
import { Stepper, Typography, Step, StepLabel } from "@material-ui/core";

function generateStep(item, index, current) {
    let data = {};
    if (current === index) data = { icon: <item.currentIcon /> };
    if (index > current) data = { icon: <item.icon /> };
    return (<Step key={index} >
        <StepLabel {...data} >{item.resumido}</StepLabel>
    </Step >)
}

export default function (props: { activeStep: number, steps: any[] }) {
    const { activeStep, steps } = props;
    return (<>
        <Stepper className="hide-on-min-height" alternativeLabel activeStep={activeStep}>
            {steps.map((x, i) => generateStep(x, i, activeStep))}
        </Stepper>

        <Typography variant="h6" className="show-on-min-height" style={{
            textAlign: "center",
            textTransform: "uppercase",
            background: "#3f51b5",
            paddingTop: 5,
            paddingBottom: 5,
            color: "white"
        }}>
            {(steps[activeStep] || {}).t}
        </Typography>
    </>);
}


