import React from "react";
import { TextField, Typography, Button } from "@material-ui/core";
import { Route } from "react-router-dom";
import InvoiceData from "./InvoiceData";
import ValidateInput from "../Services/ValidateInput";
import ValidarIdentificacion from "../Services/ValidarIdentificacion";
import Mountable from "../HOC/Mountable";

interface Props {
    history: any;
    match: any;

    requireEmail: boolean;
    requireCedula: boolean;

    data: any;
    invoiceData?: any;

    onInvoiceDataChange: any;
    onInputChange: any;
}

const style = {
    root: { padding: "20px 10px", flex: 1 },
    TextField: { marginTop: 10, marginBottom: 10 }
}
class ContactData extends React.Component<Props> {
    state = {
        telefono_error: "",
        email_error: ""
    }
    constructor(props) {
        super(props);
        if (props.onMounted) props.onMounted(props.stepIndex);
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(event) {
        const { value } = event.currentTarget;
        if (value) {
            this.props.history.push(this.props.match.path + "/invoice-data");
        } else {
            this.props.onInvoiceDataChange(null);
        }
    }

    render() {
        const { props } = this;
        const telefono_error = ValidateInput.tel(props.data.telefono);
        let telefono_props: any = {};
        if (telefono_error) {
            telefono_props.error = true;
            telefono_props.helperText = telefono_error;
        }
        const email_error = ValidateInput.email(props.data.email);
        let email_props: any = {};
        if (email_error) {
            email_props.error = true;
            email_props.helperText = email_error;
        }
        const cedula_err = props.data.identificacion
            ? ValidarIdentificacion(props.data.identificacion, props.data.documento)
            : "";
        const cedula_props: any = {};
        if (cedula_err) {
            cedula_props.error = true;
            cedula_props.helperText = cedula_err;
        }

        return (<div style={style.root}>
            {props.requireCedula &&
                (<React.Fragment>
                    <TextField
                        autoFocus={true}
                        label="Documento"
                        fullWidth
                        style={style.TextField}
                        name="contactData.documento"
                        select
                        SelectProps={{ native: true }}
                        value={props.data.documento || 1}
                        onChange={props.onInputChange}
                    >
                        <option value={1}>RUC</option>
                        <option value={2}>Cedula</option>
                        <option value={3}>Pasaporte</option>
                    </TextField>
                    <TextField
                        inputProps={{ maxLength: 20 }}
                        label="# Identificacin*"
                        fullWidth
                        style={style.TextField}
                        name="contactData.identificacion"
                        value={props.data.identificacion || ""}
                        onChange={props.onInputChange}
                        {...cedula_props}
                    />
                </React.Fragment>)}
            <TextField
                autoFocus={props.requireCedula ? false : true}
                label="Nombre (Nombres y Apellidos)*"
                fullWidth
                inputProps={{ maxLength: 300 }}
                style={style.TextField}
                name="contactData.nombre"
                value={props.data.nombre}
                onChange={props.onInputChange}
            />
            <TextField
                label="Telefono*"
                fullWidth
                inputProps={{ maxLength: 50, type: "phone" }}
                style={style.TextField}
                name="contactData.telefono"
                value={props.data.telefono}
                onChange={props.onInputChange}
                {...telefono_props}
            />
            <TextField
                label={"Email" + (props.requireEmail ? "*" : "")}
                fullWidth
                inputProps={{ maxLength: 150, type: "email" }}
                style={style.TextField}
                name="contactData.email"
                value={props.data.email}
                onChange={props.onInputChange}
                {...email_props}
            />

            <Typography variant="h6" style={{ marginTop: 10, marginBottom: 10 }} >Datos de Facturacin</Typography>
            <div style={{ width: "95%", marginBottom: 10, textAlign: "center", display: "flex", flexDirection: "row" }}>
                <Button variant={props.invoiceData ? "outlined" : "contained"} color={props.invoiceData ? "default" : "primary"}
                    onClick={this.handleClick} value="" style={{ flex: 1, marginRight: 10 }} >
                    CONSUMIDOR FINAL
                </Button>
                <Button variant={props.invoiceData ? "contained" : "outlined"} color={props.invoiceData ? "primary" : "default"}
                    size="small" value="1" style={{ flex: 1, marginLeft: 10 }} onClick={this.handleClick}>
                    {(props.invoiceData && props.invoiceData.razonSocial) || "Agregar Datos"}
                </Button>
            </div>
            <Route path={`${props.match.path}/invoice-data`}
                render={r_props => <InvoiceData {...r_props} data={props.invoiceData} onDone={props.onInvoiceDataChange} />}
            />
        </div>)
    }
}

export default Mountable(ContactData);