import React from "react";
import { Dialog, DialogContent, TextField, Button, DialogActions, withStyles } from "@material-ui/core";
import ValidarIdentificacion from "../Services/ValidarIdentificacion";
import ValidateInput from "../Services/ValidateInput";
import * as prefs from "../Services/Preferences";

const style = {
    textField: {
        marginTop: 10,
        marginBottom: 10
    },
    content: {
        paddingTop: 10,
        paddingLeft: 10,
        paddingRight: 10,
        paddingBottom: 0
    }
};

class InvoiceData extends React.PureComponent {
    state = {
        documento: 1,
        identificacion: "",
        razonSocial: "",
        telefono: "",
        direccion: "",
        email: ""
    }
    constructor(props) {
        super(props);
        if (props.data) {
            for (const key in props.data) {
                if (props.data.hasOwnProperty(key)) {
                    this.state[key] = props.data[key];
                }
            }
        } else {
            var stored_data = prefs.Get("invoiceData");
            if (stored_data) {
                for (const key in stored_data) {
                    if (stored_data.hasOwnProperty(key)) {
                        this.state[key] = stored_data[key];
                    }
                }
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleExit = this.handleExit.bind(this);
        this.handleDone = this.handleDone.bind(this);
    }
    handleChange(event) {
        const { name, value } = event.target;
        this.setState({
            [name]: value
        });
    }
    handleExit() {
        const { match, history } = this.props;
        const new_path = match.path.substr(0, match.path.indexOf("/invoice-data"));
        history.push(new_path);
    }
    handleDone(event) {
        event.preventDefault();
        const msg = validateMsg(this.state);
        if (msg) {
            alert(msg);
        } else {
            if (this.props.onDone)
                this.props.onDone(this.state);
            this.handleExit();
            prefs.Set("invoiceData", this.state);
        }
    }

    render() {
        const cedula_err = this.state.identificacion
            ? ValidarIdentificacion(this.state.identificacion, this.state.documento)
            : "";
        const cedula_props = {};
        if (cedula_err) {
            cedula_props.error = true;
            cedula_props.helperText = cedula_err;
        }
        const rs_err = (this.state.identificacion && !(this.state.razonSocial || "").trim())
            ? "Esta campo no puede estar vaco"
            : "";
        const tel_props = {};
        const tel_err = ValidateInput.tel(this.state.telefono)
        if (tel_err) {
            tel_props.error = true;
            tel_props.helperText = tel_err;
        }
        const email_props = {};
        const email_err = ValidateInput.email(this.state.email)
        if (email_err) {
            email_props.error = true;
            email_props.helperText = email_err;
        }

        const hay_errores = cedula_err || rs_err || tel_err || email_err;

        return (<Dialog open={true}>
            <form onSubmit={this.handleDone}>
                <DialogContent style={style.content}>
                    <TextField value={this.state.documento}
                        name="documento"
                        onChange={this.handleChange}
                        select
                        SelectProps={{ native: true }}
                        fullWidth
                        label="Documento"
                        style={style.textField}
                    >
                        <option value={1}>RUC</option>
                        <option value={2}>Cedula</option>
                        <option value={3}>Pasaporte</option>
                    </TextField>

                    <TextField name="identificacion"
                        inputProps={{ maxLength: 20 }}
                        onChange={this.handleChange}
                        label="# de Identificacion"
                        value={this.state.identificacion || ""}
                        style={style.textField}
                        fullWidth

                        {...cedula_props}
                    />

                    <TextField name="razonSocial"
                        inputProps={{ maxLength: 200 }}
                        onChange={this.handleChange}
                        label="Razon Social"
                        value={this.state.razonSocial || ""}
                        style={style.textField}
                        fullWidth
                    />

                    <TextField name="telefono"
                        inputProps={{ maxLength: 50 }}
                        onChange={this.handleChange}
                        label="Telefono"
                        value={this.state.telefono || ""}
                        style={style.textField}
                        fullWidth
                        {...tel_props}
                    />

                    <TextField name="direccion"
                        inputProps={{ maxLength: 300 }}
                        onChange={this.handleChange}
                        label="Direccion"
                        value={this.state.direccion || ""}
                        multiline
                        rows={2}
                        style={style.textField}
                        fullWidth
                    />

                    <TextField name="email"
                        inputProps={{ maxLength: 150 }}
                        onChange={this.handleChange}
                        label="Email"
                        value={this.state.email || ""}
                        style={style.textField}
                        fullWidth
                        {...email_props}
                    />

                </DialogContent>
                <DialogActions>
                    <Button type="button" onClick={this.handleExit} color="secondary" variant="outlined">
                        SALIR
                </Button>
                    <Button disabled={hay_errores} type="submit" color="primary" variant="outlined">
                        AGREGAR
                </Button>
                </DialogActions>
            </form>
        </Dialog>)
    }
}

function validateMsg(state) {
    const { documento, identificacion, razonSocial, telefono, direccion, email } = state;
    let a = identificacion || "";
    let b = razonSocial || "";
    let c = telefono || "";
    let d = direccion || "";
    let e = email || "";
    let msg = "";
    if (a.length || b.length || c.length || d.length || e.length) {
        if (!a.length) return "Agregue el numero de identificacion";
        if (!b.length) return "Agregue la Razon Social";
        msg = ValidarIdentificacion(identificacion, documento);
        if (msg) return msg;
        msg = c && ValidateInput.tel(c);
        if (msg) return msg;
        msg = e && ValidateInput.email(e);
        if (msg) return msg;
    }
}

export default withStyles(theme => ({
    paper: {
        maxHeight: "calc(100% - 30px)",
        marginTop: 15,
        marginBottom: 15
    }
}))(InvoiceData);