import React from "react";
import { Route } from "react-router-dom";

class MountableComponent extends React.PureComponent<any, any> {
    componentWillMount() {
        if (this.props.onMounted) this.props.onMounted(
            +this.props.match.path.replace("/", "")
        );
    }
    render() {
        const { onMounted, renderProp, ...remain } = this.props;
        return this.props.children;
    }
}

interface Props {
    onMounted: (path) => void;
    render: (props: any) => any;
    path: string;
}

export default function (props: Props) {
    const { render, path, onMounted } = props;
    return (<Route
        path={path}
        render={routeProps =>
            <MountableComponent
                {...routeProps}
                onMounted={onMounted}
            >
                {render(routeProps)}
            </MountableComponent>}
    />);
}