import React from 'react';
import { Button } from '@material-ui/core';
import { ArrowBack } from '@material-ui/icons';

export default function (props) {
    return (<div style={{ width: "calc(100% - 20px)", display: "flex", flexDirection: "row", textAlign: "center", margin: 10, minHeight: 40 }}>
        {props.activeStep === 0
            ? null
            : <Button
                onClick={props.onBackClick}
                title="Regresar"
                color="secondary"
                variant="outlined"
            >
                <ArrowBack />
            </Button>}
        <Button
            onClick={props.onNextClick}
            disabled={props.disabled}
            color="primary"
            type="button"
            variant="outlined"
            style={{ flex: 1 }}
            type="submit"
            size="large">
            CONTINUAR
  </Button>
    </div>)
}