import React from "react";
import { TextField, InputAdornment, IconButton, Card, CardContent, Typography } from "@material-ui/core";
import { withGoogleMap, GoogleMap, withScriptjs } from "react-google-maps";
import { MarkerWithLabel } from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { GpsFixed, GpsOff } from "@material-ui/icons";
import Geo from "../Services/Geo";
import * as prefs from "../Services/Preferences";

export default class extends React.PureComponent {
    state = {
        zoom: 15,
        initial_position: undefined,
        initial_gps_enabled: undefined
    }
    /** objeto Autocomplete | referencia del textbox */
    autocompleteRef;
    /** objeto Google Maps | referencia del Mapa */
    mapRef;
    /** div que representa el Marker estatico */
    virtualMarker;

    constructor(props) {
        super(props);
        this.placeChanged = this.placeChanged.bind(this);
        this.handleLocationClick = this.handleLocationClick.bind(this);
        this.handleInput = this.handleInput.bind(this);

        this.handleMapMounted = this.handleMapMounted.bind(this);
        this.handleDragEnd = this.handleDragEnd.bind(this);
        this.handleDragStart = this.handleDragStart.bind(this);
        this.handleCenterChanged = this.handleCenterChanged.bind(this);

        this.positionChanged = this.positionChanged.bind(this);
        this.valueChanged = this.valueChanged.bind(this);

        console.info("Location comienza con direccion", props.data);
        var direccion = prefs.Get("direccion");
        if (direccion && !props.data.position) {
            console.info("Se reemplaza por la direccion en PREFS");
            props.onDataChange(props.prop, direccion);
            this.state.zoom = 17;
            this.state.initial_position = direccion;
        }
        if (props.data.position) {
            this.state.zoom = 17;
            this.state.initial_position = props.data.position;
        }
        this.state.initial_gps_enabled = props.data && props.data.gps_enabled;
    }
    componentDidMount() {
        if (this.state.initial_position) {
            if (this.state.initial_gps_enabled === undefined ||
                this.state.initial_gps_enabled === null) {
                console.info("Se verifica si el GPS se puede usar");
                Geo.CanUseGPS()
                    .then(() => this.valueChanged("gps_enabled", true))
                    .catch(() => this.valueChanged("gps_enabled", false));
            }
        } else {
            // pongo la posicion actual POR DEFAULT
            console.info("No se encuentra la posicion, asi que se busca por GPS/HTTP");
            Geo.currentPosition()
                .then((center) => {
                    console.info("Posicion por GPS/HTTP encontrada");
                    this.positionChanged(center, false);
                    this.valueChanged("gps_enabled", true);
                    this.setState({ zoom: 17 });
                }).catch(_ => {
                    console.info("Posicion por GPS/HTTP no encontrada");
                    this.setState({ loading: false });
                    this.valueChanged("gps_enabled", false);
                });
        }
    }
    componentWillUnmount() {
        if (this.autocomplete)
            this.autocomplete.unbindAll();
    }
    valueChanged(key, value) {
        const data = { ...this.props.data };
        data[key] = value;
        this.props.onDataChange(this.props.prop, data);
    }

    positionChanged(position, avoid_update_zoom) {
        if (!avoid_update_zoom && this.mapRef && this.mapRef.getZoom() < 17) {
            this.setState({ zoom: 17 });
        }
        this.valueChanged("position", position);
    }
    handleInput(event) {
        const { value, name } = event.currentTarget;
        this.valueChanged(name, value);
    }

    handleCenterChanged() {
        this.positionChanged(this.mapRef && this.mapRef.getCenter().toJSON());
    }
    // cuando encuentra una direccion, o aplasta ENTER
    placeChanged() {
        const place = this.autocomplete.getPlace();
        // un texto y ya. Busco lo mas cercano
        if (!place.geometry)
            return Geo.CoordsFromPlace(place.name)
                .then(place => this.positionChanged(place.geometry.location));

        this.positionChanged(place.geometry.location);
        this.valueChanged("direccion", this.autocompleteRef.value);
    }
    handleLocationClick() {
        Geo.currentPosition()
            .then(location => {
                this.positionChanged(location);
                Geo.PlaceFromCoords(this.props.data.position)
                    .then(place => this.valueChanged("direccion", place.inputText));
            }).catch(ex => {
                alert("No pudimos obtener tu ubicacion");
                console.warn("handleLocationClick", ex);
            });
    }

    handleDragStart() {
        this.virtualMarker.classList.add("on-move");
    }
    handleDragEnd() {
        this.positionChanged(this.mapRef.getCenter().toJSON(), true);
        Geo.PlaceFromCoords(this.mapRef.getCenter())
            .then(d => this.valueChanged("direccion", d.inputText))
            .catch(ex => console.warn("PlaceFromCoords ERROR", ex));
        this.virtualMarker.classList.remove("on-move");
    }

    handleMapMounted(map) {
        this.mapRef = map;
        if (!this.mapRef) return;
        this.virtualMarker = document.createElement("div");
        this.virtualMarker.className = "centerMarker";
        map.getDiv().appendChild(this.virtualMarker);
        this.mountAutocomplete();
    }
    mountAutocomplete() {
        if (!(this.mapRef && this.autocompleteRef)) return;
        if (this.autocomplete) return;
        // cuando se ubica la ubicacion geoLocation pero no se ha actualizado
        // la direccion en el CUADRO DE TEXTO
        if (this.props.data.position && !this.props.data.direccion) {
            Geo.PlaceFromCoords(this.props.data.position)
                .then(place => this.valueChanged("direccion", place.inputText));
        }
        this.autocomplete = new window.google.maps.places.Autocomplete(this.autocompleteRef);
        this.autocomplete.bindTo('bounds', this.mapRef);
        this.autocomplete.addListener("place_changed", this.placeChanged);
        this.autocomplete.setOptions({ strictBounds: true });
    }

    render() {
        const { data, store } = this.props;
        const { zoom } = this.state;
        return (<div style={{ overflow: "auto", paddingLeft: 10, paddingRight: 10, flex: 1 }}>
            <div style={{ width: "100%", display: "flex", flexDirection: "row" }}>
                <TextField
                    inputRef={ref => this.autocompleteRef = ref}
                    name="direccion"
                    inputProps={{ maxLength: 200 }}
                    onChange={this.handleInput}
                    value={data.direccion || ""}
                    placeholder="Ej. Calle 1 y Calle 2"
                    label="Direccion de Entrega*"
                    fullWidth
                    style={{ marginBottom: 10, marginTop: 5, flex: 1 }}
                    InputProps={{
                        endAdornment: (<InputAdornment position="end" >
                            <IconButton onClick={this.handleLocationClick} disabled={!data.gps_enabled}>
                                {data.gps_enabled ? <GpsFixed /> : <GpsOff />}
                            </IconButton>
                        </InputAdornment>)
                    }}
                />
            </div>

            <Map
                googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${store.G_M_K_l || ""}&v=3.exp&libraries=geometry,places,geocode`}
                //googleMapURL="https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places"
                containerElement={<div id="map-container" />}
                loadingElement={<div style={{ height: `100%` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                onClick={this.handleMapClick}
                zoom={zoom}
                onZoomChanged={() => this.setState({ zoom: this.mapRef.getZoom() })}
                onMapMounted={this.handleMapMounted}
                center={data.position || store.position}
                onCenterChanged={this.handleCenterChanged}
                onDragStart={this.handleDragStart}
                onDragEnd={this.handleDragEnd}
                marker={{
                    label: "Tienda",
                    position: store.position
                }}
            //onBoundsChanged={this.handleBoundsChanged}
            />

            <TextField
                name="referencia"
                value={data.referencia || ""}
                onChange={this.handleInput}
                inputProps={{ maxLength: 200, id: "referencia" }}
                label="Referencia*"
                placeholder="Ej. Al lado de la panaderia 'El Pan de Cada Dia'"
                fullWidth
            />

        </div>)
    }
}

const Map = withScriptjs(withGoogleMap(function Map(props) {
    const google = window.google;
    const { marker, onMapMounted, ...mapProps } = props;
    return (<GoogleMap
        {...mapProps}
        ref={onMapMounted}
        options={{
            disableDefaultUI: true,
            zoomControl: true,
            clickableIcons: false
        }}
    >
        {marker && <MarkerWithLabel
            labelAnchor={new google.maps.Point(0, 0)}
            position={marker.position}
            icon="./store.png"
        >
            <Card elevation={1} color="secondary" style={{ margin: 2 }}>
                <CardContent style={{ maxWidth: 200, padding: 5 }}>
                    <Typography variant="button">{marker.label}</Typography>
                </CardContent>
            </Card>
        </MarkerWithLabel>}
    </GoogleMap>)
}));