import React from 'react';
import App from './App';
import ReactDOM from 'react-dom';
import Home from './Home';
import Options from './Options';
import API from "./Services/API";

var PRE_LOADED_DIRECCION: any;
var params: any;
if (window.location.search && window.URLSearchParams) {
    params = new URLSearchParams(window.location.search);
    console.info(window.location.search);

    var direccion = params && params.get("step1");
    try {
        direccion = atob(direccion) && JSON.parse(atob(direccion));
    } catch (ex) {
        direccion = null;
    }
    PRE_LOADED_DIRECCION = direccion;
}

API.GetStoreData()
    .then(store => {
        if (store.estado === "DISPONIBLE" && PRE_LOADED_DIRECCION) {
            return LaunchApp({ store, direccion: PRE_LOADED_DIRECCION });
        }
        if (params && params.get("options")) {
            return LaunchOptions();
        }
        return LaunchHome({ store, direccion: PRE_LOADED_DIRECCION });
    }).catch(error => LaunchHome({ error }));


function LaunchApp(props: any) {
    ReactDOM.render(<App {...props} />, document.getElementById('app'));
}
function LaunchHome(props: any) {
    ReactDOM.render(<Home {...props} launch={LaunchApp} />, document.getElementById('app'));
}
function LaunchOptions() {
    ReactDOM.render(<Options />, document.getElementById('app'));
}


