import { isDigit } from "./ValidarIdentificacion";

const ValidateInput = {
    tel(value) {
        if (!value) return;
        if (value.length < 7) return "El nmero de telefono debe tener al menos 7 digitos";
        for (let i = 0; i < value.length; i++) {
            if (!(value[i] === "+" || isDigit(value[i]) || value[i].trim() === "")) {
                return "El nmero debe tener uno de estos formatos: '09123456789' o '+5931234567890'";
            }
        }
    },
    email(value) {
        if (!value) return;
        if (value.length < 5) return "El email no est escrito correctamente";
        let twoParts = value.split("@");
        if (twoParts.length !== 2) return "El email no est escrito correctamente";
        if (!(twoParts[1].indexOf(".") >= 0)) return "El email no est escrito correctamente";
        if (value.trim().indexOf(" ") >= 0) return "El email no est escrito correctamente";
        if (value.trim().indexOf("\t") >= 0) return "El email no est escrito correctamente";
        if (value.trim().indexOf("\r") >= 0) return "El email no est escrito correctamente";
    }
}
export default ValidateInput;