import React from "react";
import { CheckCircleOutline, Edit } from "@material-ui/icons";
import { Typography, Button, CircularProgress } from "@material-ui/core";
import Mountable from "../HOC/Mountable";

function Done(props) {
    const { loading, error, ...other_props } = props;
    function handleReturnToOrder() {
        props.history.push("/1");
    }
    return (<div style={{ textAlign: "center", overflow: "auto", padding: 10 }}>
        {loading
            ? <Wait />
            : (error
                ? <ErrrMsg {...error} onClick={handleReturnToOrder} />
                : <IsCompleted {...other_props} />)}
    </div>);
}
export default Mountable(Done);

function Wait(props) {
    return (<React.Fragment>
        <CircularProgress size={50} />
        <Typography variant="h6">Guardando...</Typography>
    </React.Fragment>);
}
function ErrrMsg(props) {
    return (<React.Fragment>
        <Typography variant="h6" color="secondary" >Ocurri un problema al guardar tu orden. Regresa al pedido, verifica tu informacin e intentalo nuevamente.</Typography>

        <Typography variant="body1" color="secondary" >{props.message}</Typography>
        <Typography variant="caption" color="secondary" >{props.code}{props.status && (" | " + props.status)}</Typography>
        {props.statusText && <Typography variant="body1" color="secondary" >{props.statusText}</Typography>}

        <Button onClick={props.onClick} variant="outlined" color="primary" fullWidth style={{ marginTop: 20 }} >
            REGRESAR AL PEDIDO <Edit />
        </Button>
    </React.Fragment>);
}
function IsCompleted(props) {
    const ws = CreateWSMessage(props);
    return (<React.Fragment>
        <CheckCircleOutline fontSize="large" style={{ margin: 20 }} />
        <Typography variant="h5">Tu pedido ha sido recibido!</Typography>
        <Typography variant="body2" style={{ maxWidth: 300, margin: "auto", textAlign: "center" }}>
            Nos comunicaremos contigo para coordinar la entrega de tu pedido.Mantente pendiente a tu telefono.
     </Typography>
        <Button onClick={ev => window.location.replace(window.location.pathname)} style={{ margin: 10 }} variant="contained" color="primary">NUEVO PEDIDO</Button>
        {ws && (<Button target="_blank" style={{ margin: 10 }} variant="outlined" color="secondary" component="a" href={ws}>
            CHATEA CON NOSOTROS
            <img alt="Whatsapp" src="./ws.png" style={{ height: 20, width: 20, marginLeft: 5 }}></img>
        </Button>)}
    </React.Fragment>);
}

function CreateWSMessage(info) {
    const { ws_phone, ws_msg, contactData, orderNumber } = info;
    if (!ws_phone || !ws_msg) return "";
    const final_msg = ws_msg
        .replace(/__NAME__/g, (contactData || {}).nombre)
        .replace(/__ORDER_NUMBER__/g, orderNumber || "");
    return "whatsapp://send?" +
        (`phone=${ws_phone}&`) +
        `text=${encodeURIComponent(final_msg)}`;

}