import React from "react";
import { withStyles, GridList, GridListTile, GridListTileBar, Typography } from "@material-ui/core";


const styles = {
    root: {
        display: 'flex',
        flexWrap: 'wrap',
        justifyContent: 'space-around',
        overflow: 'hidden'
    },
    gridList: {
        flexWrap: 'nowrap',
        // Promote the list into his own layer on Chrome. This cost memory but helps keeping high FPS.
        transform: 'translateZ(0)',
    }
};

export default withStyles({
    tile_no_disponible: {
        "&::before": {
            background: "#b3b1b1d4",
            position: "absolute",
            top: 0,
            left: 0,
            height: "100%",
            width: "100%",
            content: '"NO DISPONIBLE"',
            zIndex: 100,
            color: "white",
            paddingTop: 60,
            fontSize: "calc(1em + 1vw)",
            fontWeight: "bolder",
            fontFamily: '"Roboto", "Helvetica", "Arial", sans-serif',
            textAlign: "center"
        }
    }
})(function (props: any) {
    return (<div style={{ ...props.style }}>
        <Typography variant="h6" style={{ marginLeft: 10 }}>{props.title}</Typography>
        <GridList cols={2.5} style={styles.gridList as any}>
            {props.items.sort((a, b) => a.orden - b.orden).map((item) => {
                const found = (props.choosenItems || []).find(x => x.id === item.id);
                return generateCarouselItem(item, found && found.cantidad, props.onClick, props.classes);
            })}
        </GridList>
    </div>)
});
function generateCarouselItem(item, cantidad, onClick, classes) {
    const TileBar: any = {
        title: item.nombre,
        actionPosition: "right",
        subtitle: "$ " + item.pvp_final.toFixed(2),
        actionIcon: undefined as any
    }
    if (cantidad) TileBar.actionIcon = <Typography variant="body2" style={{ color: "#fff", marginRight: 5 }} >x{cantidad}</Typography>;
    const Tile: any = { cursor: "pointer" };

    const _on_click = (event: any) => {
        if (item.paused) {
            alert("Este item no est disponible");
        } else {
            onClick(event);
        }
    }

    if (cantidad) Tile.border = "3px dashed #f50057";
    return (<GridListTile
        classes={{ tile: item.paused ? classes.tile_no_disponible : null }}
        style={Tile as any}
        key={item.id}
        onClick={_on_click}
        id={item.id}
        color="inherit">
        <img src={item.imgSrc} alt={item.nombre} />
        <GridListTileBar {...TileBar} />
    </GridListTile >);
}