import ValidateInput from './ValidateInput';
import ValidarIdentificacion from './ValidarIdentificacion';

export function VerifyStep(step, state) {
    var store = state.store || {};
    // ubicacion
    if (step === 0) {
        const x = state.direccion;
        if (!x.position) return "Confirme su ubicacin en el mapa.";
        if (!x.direccion) return "Digite su direccin.";
        if (!x.referencia) {
            Focus("referencia");
            return "Agregue la referencia de la direccin de entrega.";
        };
    }

    // elegir productos
    if (step === 1 || step === 2) {
        if (!state.items.length)
            return "Para continuar seleccione al menos un producto";
    }

    if (step === 2 && store.estado !== "ACTIVO" && !state.envio_programado)
        return "Debido a que la tienda no esta disponible en este momento, necesitas programar una fecha de entrega";


    if (step === 3 && state.contactData) {
        const x = state.contactData;
        if (!x.nombre) return "Agregue un nombre de contacto";
        if (!x.telefono) return "Agregue un telefono de contacto";
        if (store.requireContactCedula) {
            if (!x.identificacion) return "Agregue el nmero de identificacin de contacto";
            msg = ValidarIdentificacion(x.identificacion, x.documento);
            if (msg) return msg;
        }
        let msg = ValidateInput.tel(x.telefono);
        if (msg) return msg;
        if (store.requireContactEmail && !x.email)
            return "Agregue el Correo Electrnico de contacto";
        msg = ValidateInput.email(x.email);
        if (msg) return msg;
    }

    if (step === 4) {
        if (!state.forma_pago) {
            return "Seleccione su mtodo de pago";
        }
        if ((+state.forma_pago) === 3 && !state.numeroTransaccion) {
            return "Agregue el nmero de Comprobante";
        }
    }

    return;
}
function Focus(ID) {
    window.requestAnimationFrame(() => {
        try {
            document.getElementById("referencia").focus();
        } catch (ex) { }
    })
}