function DayString(day) {
    if (day === 0) return "Dom";
    if (day === 1) return "Lun";
    if (day === 2) return "Mar";
    if (day === 3) return "Mie";
    if (day === 4) return "Jue";
    if (day === 5) return "Vie";
    if (day === 6) return "Sab";
    if (day === 7) return "Dom";
}
export function ParseHorarios(horarios) {
    var results = [];
    if (horarios && horarios.length) {
        for (let i = 0; i < horarios.length; i++) {
            const h = horarios[i];
            var dias_ordenados = h.dias
                .map(x => x === 0 ? 7 : x)
                .sort((a, b) => a - b);

            var dias_consecutivos = dias_ordenados.length > 1 && dias_ordenados
                .filter((value, index) => {
                    if (index === 0) return true;
                    return dias_ordenados[index - 1] === (value - 1);
                }).length === dias_ordenados.length;
            var periodo = PeriodoString(h);
            var dias;
            if (dias_consecutivos) {
                dias = DayString(dias_ordenados[0]) + " - " + DayString(dias_ordenados[dias_ordenados.length - 1]);
            } else {
                dias = dias_ordenados.map(x => DayString(x)).join(", ");
            }
            results.push({ periodo, dias });
        }
    }
    return results;
}

function PeriodoString(h) {
    var { tz, desde, hasta } = h;
    var t = new Date();
    var desde = new Date(Date.UTC(t.getFullYear(), t.getMonth(), t.getDate(), desde[0], parseInt(desde[1]) + parseInt(tz), 59));
    var hasta = new Date(Date.UTC(t.getFullYear(), t.getMonth(), t.getDate(), hasta[0], parseInt(hasta[1]) + parseInt(tz), 59));
    var PLUS = "";
    if (desde.getDay() < hasta.getDay()) {
        PLUS = "+" + (hasta.getDay() - desde.getDay());
    }
    return desde.toLocaleTimeString().replace(/:59/g, "") + " - " + hasta.toLocaleTimeString().replace(/:59/g, "") + PLUS
}

/** 
 * Utiliza una lista de horarios y los genera en DIAS del ao
 * Si no hay horarios, genera un numeros de dias del ao y listo
 */
export function HorariosToDias(horarios, dias_a_generar) {
    var dias = [];
    // en este formato
    // const dia = { day: 1, horas: [9, 10, 11, 12, 24, 56] };
    var CURRENT_TZ = new Date().getTimezoneOffset();
    if (horarios && horarios.length) {
        for (let i = 0; i < horarios.length; i++) {
            const H = horarios[i];
            var dias_ordenados = H.dias.sort((a, b) => a - b);
            // se quee los minutos pasan de 15 en 15
            var min_desde = (H.desde[0] * 60) + H.desde[1] + H.tz - CURRENT_TZ;
            var min_hasta = (H.hasta[0] * 60) + H.hasta[1] + H.tz - CURRENT_TZ;

            dias_ordenados.forEach(d => {
                var exists = dias.find(x => x.day === d);
                if (!exists) {
                    exists = { day: d, horas: [] };
                    dias.push(exists);
                }
                // verifico si las horas ya existen
                for (let index = min_desde; index <= min_hasta; index += 15) {
                    if (exists.horas.indexOf(index) < 0) {
                        exists.horas.push(index);
                    }
                }
            });
        }
    }

    var result = [];
    if (!dias_a_generar) dias_a_generar = 7;
    var hoy = new Date();
    var dias_calenario_desde_hoy = 0;
    while (dias_a_generar) {
        dias_calenario_desde_hoy++;
        var sgte_dia = new Date(hoy.getFullYear(), hoy.getMonth(), hoy.getDate() + dias_calenario_desde_hoy, hoy.getHours(), hoy.getMinutes());
        var dia_en_horario = dias.find(item => item.day === sgte_dia.getDay());

        var day = [sgte_dia.getFullYear(), sgte_dia.getMonth(), sgte_dia.getDate(), sgte_dia.getTimezoneOffset()].join("-");
        if (dias.length) {
            if (dia_en_horario) {
                result.push({ ...dia_en_horario, Date: sgte_dia, day });
                dias_a_generar--;
            }
        } else {
            result.push({ day, Date: sgte_dia });
            dias_a_generar--;
        }
    }
    return result;
}

window.HorariosToDias = HorariosToDias;