import React from "react";
import { Typography, TextField, List, Button } from "@material-ui/core";
import { Route } from "react-router-dom";
import ViewProduct from "../TakeOrder/ViewProduct";
import Schedule from "./Schedule";
import { generateRowItem, generateTotals } from "./Rows";
import { ItemChanged } from "../../ActionCreators/Items";
import Mountable from "../../HOC/Mountable";
import { connect } from "react-redux";

interface Props {
    history: any;
    match: any;

    productList: Product[];
    items: Product[];

    onDataChange: any;
    onInputChange: any;
    onItemChange:any;

    delivery: {
        valor: number,
        status: any
    };
    nota: string;
    store: any;
    envio_programado: boolean;
}
class ConfirmOrder extends React.PureComponent<Props, any> {
    constructor(props) {
        super(props);
        this.handleClick = this.handleClick.bind(this);
        this.handleEnvio = this.handleEnvio.bind(this);
        this.programarEnvio = this.programarEnvio.bind(this);
        this.ProductViewClosed = this.ProductViewClosed.bind(this);
        this.FindProduct = this.FindProduct.bind(this);
    }
    handleClick(event) {
        const id = event.currentTarget.id;
        this.props.history.push(this.props.match.url + "/view-product/" + id);
    }
    programarEnvio(value?) {
        this.props.onDataChange && this.props.onDataChange("envio_programado", value);
        this.props.history.push(this.props.match.path);
    }
    handleEnvio(event) {
        const programar_envio = event.currentTarget.value;
        if (programar_envio) {
            this.props.history.push(this.props.match.path + "/schedule");
        } else {
            this.programarEnvio();
        }
    }
    ProductViewClosed(product) {
        if (product) this.props.onItemChange(product);
        this.props.history.push(this.props.match.path);
    }
    FindProduct(ID?) {
        if (!ID) return undefined;
        var exists = (this.props.items || []).find(x => x.id === +ID);
        if (exists) return exists;
        return this.props.productList.find(x => x.id === +ID);
    }
    render() {
        console.log("ConfirmOrder rendered");
        const { props } = this;
        const deliveryCost = (props.delivery || {}).valor;
        const deliveryStatus = (props.delivery || {}).status;

        return (<div style={{ paddingLeft: 10, paddingRight: 10, overflow: "auto", flex: 1 }}>
            <List style={{ marginBottom: 10 }} dense={true}>
                {(props.items).map(x => generateRowItem(x, this.handleClick))}

                {(deliveryStatus === "ACTIVO" && !isNaN(+deliveryCost))
                    ? generateRowItem({
                        id: "DOMICILIO",
                        nombre: "ENVIO A DOMICILIO"/* + (props.delivery.metros
                            ? (" (" + (props.delivery.metros / 1000).round(1) + " km)")
                            : "")*/,
                        pvp_final: deliveryCost,
                        cantidad: 1
                    }) : null}

                {generateTotals(props.items, deliveryCost, props.store.porcentajeIVA)}
            </List>
            {deliveryStatus !== "ACTIVO"
                ? (<Typography variant="subtitle1" color="secondary">
                    El costo del Envo a Domicilio no est disponible.
                    Debes consultar este valor con la tienda.
                </Typography>)
                : null}

            <div style={{ width: "100%", textAlign: "center" }}>
                <TextField
                    label="Agrega una nota a tu pedido"
                    placeholder="Ej. Sin cubiertos, sin salas extra, etc."
                    multiline={true}
                    name="nota"
                    inputProps={{ maxLength: 200 }}
                    value={props.nota}
                    onChange={props.onInputChange}
                    rows={2}
                    style={{ width: "95%", margin: "auto", marginBottom: 10, marginTop: 10 }}
                />
                <div style={{ width: "95%", display: "flex", flexDirection: "row", marginTop: 10 }}>
                    {props.store.estado === "ACTIVO" &&
                        (<Button color={props.envio_programado ? "default" : "primary"} style={{ flex: 1, marginRight: 5 }} variant={props.envio_programado ? "outlined" : "contained"} onClick={this.handleEnvio} value="" >
                            Entrega inmediata
                        </Button>)}
                    <Button color={props.envio_programado ? "primary" : "default"} style={{ flex: 1, marginLeft: 5 }} variant={props.envio_programado ? "contained" : "outlined"} onClick={this.handleEnvio} value="1">
                        {props.envio_programado ? "Entrega programada" : "Programa la Entrega"}
                    </Button>
                </div>
            </div>
            <ViewProduct
                onDone={this.ProductViewClosed}
                findProductFn={this.FindProduct}
                path={props.match.path + "/view-product/:id"}
            />
            <Route path={props.match.path + "/schedule"} >
                <Schedule store={props.store} value={props.envio_programado} onDone={this.programarEnvio} />
            </Route>
        </div >)
    }
}

const mapStateToProps = state => {
    return {
        productList: state.productList,
        items: state.items
    }
}
const mapDispatchToProps = {
    onItemChange: ItemChanged
}

export default Mountable(
    connect(mapStateToProps, mapDispatchToProps)(ConfirmOrder)
);

