export default function (detalle, PORCENTAJE_IVA) {
    const mutable = {
        detalle: detalle,
        base0: 0,
        baseIva: 0,
        subtotal: 0,
        iva: 0,
        servicio: 0,
        // propina: 0, // propina es ingresado
        descuentoTotal: 0,
        total: 0
    };

    for (var i = 0; i < detalle.length; i++) {
        const d = detalle[i];
        if (d.cantidad > 0) {
            let neto = d.cantidad * d.pvp;
            if (d.pagaIva) {
                mutable.baseIva = mutable.baseIva + neto;
            } else {
                mutable.base0 = mutable.base0 + neto;
            }
        }
    }
    mutable.base0 = mutable.base0.round(2);

    let iva = 0;
    let baseIva = 0;

    if (PORCENTAJE_IVA && mutable.baseIva) {
        iva = mutable.baseIva * PORCENTAJE_IVA / 100;
        baseIva = mutable.baseIva;
        mutable.iva = iva.round(2);
    }
    mutable.baseIva = mutable.baseIva.round(2);

    let total_con_decimales = (iva + baseIva).round(2);
    let total_redondeado = mutable.baseIva + mutable.iva;
    if ((total_redondeado - total_con_decimales).round(2) === 0.01) {
        // manualmente le quito ese 1 centavo que esta demas!
        console.info("Le quito un centavo por diferencia", total_con_decimales, total_redondeado)
        mutable.baseIva = mutable.baseIva - 0.01;
    }

    mutable.servicio = mutable.servicio.round(2);
    mutable.subtotal = (mutable.base0 + mutable.baseIva).round(2);
    // aqui tengo que borrar la columna de propina
    mutable.total = mutable.subtotal + mutable.servicio + mutable.iva + (isNaN(mutable.propina) ? 0 : mutable.propina);
    // a veces, en la suma aparecen demasiados decimales
    // por lo tanto lo redondeo (que tambien hace .toFixed)
    mutable.total = mutable.total.round(2);
    return mutable;
}