export function Set(key, item) {
    window.localStorage.setItem(key, item);
    console.log("Preferences." + key + " stored");
}
export function Get(item) {
    let result = window.localStorage.getItem(item);
    try {
        result = JSON.parse(result);
    } catch (ex) {
        console.warn("Preferences." + item, ex);
    }
    if (result === "true" || result === "false") return result === "true";
    console.log("Preferences." + item + " sent", result);
    if (result === null || result === undefined) return null;
    return result;
}