import React from "react";
import { Typography, Button, Divider } from "@material-ui/core";
import { ParseHorarios } from "./Services/HorarioCalcs";
// aqui cargo todo lo relacionado a la informacin inicial antes de comenzar!
export default class extends React.Component {
    state = {};
    constructor(props) {
        super(props);
        const { store, error } = props;
        if (store) {
            this.state.logoSrc = store.logoSrc;
            this.state.slogan = store.slogan;
            this.state.titulo = store.titulo;
            if (store.estado === "NO_DISPONIBLE") {
                this.state.error = "La tienda no se encuentra disponible por el momento. Intenta nuevamente despus.";
                this.state.horarios_de_atencion = ParseHorarios(store.horarios);
                if (this.state.horarios_de_atencion && this.state.horarios_de_atencion.length) {
                    this.state.infoHeader += "\nHorarios de Atencin:";
                }
            } else if (store.estado === "CERRADO") {
                this.state.infoHeader = "La tienda se encuentra cerrada en este momento, pero an puedes agregar tu pedido para otro da.";
                this.state.horarios_de_atencion = ParseHorarios(store.horarios);
                if (this.state.horarios_de_atencion && this.state.horarios_de_atencion.length) {
                    this.state.infoHeader += "\nHorarios de Atencin:";
                }
            } else if (store.estado === "DESHABILITADO") {
                this.state.error = "Esta tienda est DESHABILITADA y no recibe pedidos.";
                this.state._error = "Ingresaste a: " + window.location.href;
            }
        } else {
            if (error) {
                if (error.status === 404) {
                    this.state.error = "La tienda que buscas no existe. Revisa tu enlace.";
                    this.state._error = "Ingresaste a: " + window.location.href;
                } else {
                    this.state.error = error.message;
                    this.state.code = error.code || error.status;
                    this.state._error = error.statusText;
                }
            }
        }
    }
    render() {
        const { logoSrc, slogan, titulo, error, _error, horarios_de_atencion, infoHeader } = this.state;
        return (<div style={{ width: "calc(100% - 20px)", maxWidth: 600, textAlign: "center", paddingTop: 30, margin: "auto" }}>
            {error
                ? (<React.Fragment>
                    <Typography color="secondary" variant="h6">{error}</Typography>
                    <Typography color="secondary" variant="subtitle2">{_error}</Typography>
                </React.Fragment>)
                : (<React.Fragment>
                    {logoSrc && <img src={logoSrc} style={{ maxWidth: "100%", maxHeight: 250, marginTop: 10, marginBottom: 10 }} alt={titulo || ""} />}
                    <Typography variant="h5">{titulo || "Cargando..."}</Typography>
                    {slogan && <Typography variant="subtitle2">{slogan}</Typography>}

                    {infoHeader && horarios_de_atencion && <Divider />}

                    {infoHeader && <Typography color="secondary" variant="h6">{infoHeader}</Typography>}

                    {horarios_de_atencion && horarios_de_atencion.map((h, index) =>
                        <Typography key={index} color="secondary" variant="subtitle1" style={{ flex: 1, textAlign: "center" }}>{h.dias} {h.periodo}</Typography>
                    )}

                    {(titulo || logoSrc)
                        && (<Button color="primary" onClick={x => this.props.launch(this.props)} fullWidth variant="outlined" style={{ marginTop: 20 }} >
                            EMPECEMOS!
                        </Button>)}
                </React.Fragment >)
            }
        </div>)
    }
}